
body{
  margin: 0;
  padding:0;
  color: #2b413d;
}

h1 {
  font-family: 'Roboto';
}

h2 {
  font-family: 'Open Sans';
}

h3 {
  font-family: 'Roboto';
}

h4 {
  font-family: 'Roboto';
}

p {
  font-family: 'Roboto';
}

a{
  color:#333;
  font-family: 'Roboto';
}

.container{
  max-width: 750px;
  min-width: auto;
  margin: auto;
  padding: 50px;
}

.background-primary{
  background-color: #eff6f6;
}

.hero-image{
  display: flex;
  width: 150px;
  height: 150px;
  background: #fff;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0,0,0,0.125);
  border-radius: 100%; 
  margin: auto;
}

.hero-image img{
  width: 85%;
  height: 85%;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0,0,0,0.125);
  border-radius: 100%; 
  margin: auto;
}

.hero-title{
  font-family: 'Open Sans';
  margin-top: 20px;
  margin-bottom: 5px;
  font-size: 3rem;
  font-weight:700;
  text-align:center;
  color:#2b413d;
}

.header{
  font-family: 'Open Sans';
  margin-top: 20px;
  margin-bottom: 5px;
  font-size: 3rem;
  font-weight:700;
  color:#2b413d;
}

.hero-subtitle{
  font-family: 'Roboto';
  font-weight: 300;
  margin:0;
  font-size: 1.5rem;
  text-align:center;
  color:#b7c5c2;
}

.navbar-title{
  text-transform: uppercase;
  text-decoration: none;
  font-size: initial;
  letter-spacing: .1em;
  font-weight: bold !important;
}